.cap {
  position: absolute;
  transform: translateY(-40px);
  z-index: 1;
  animation: capAnimation 1s linear 0.3s forwards;
}

.next {
  position: absolute;
  transform: translateY(330px);
  z-index: 1;
  animation: nextAnim 1s linear 0s forwards;
}

@keyframes nextAnim {
  0% {
    transform: translateY(340px);
  }
  100% {
    transform: translateY(420px);
  }
}

.circle {
  border-radius: 100%;
  width: 295px;
  height: 295px;
  border-color: #13395b;
  border-width: 3.5px;
  margin-top: 5rem;
  z-index: -3;
  animation: circleAnimation 1s linear 0.3s forwards;
}

@keyframes capAnimation {
  0% {
    transform: translateY(-40px);
  }
  100% {
    transform: translateY(340px);
  }
}

@keyframes circleAnimation {
  from {
    transform: scale(0.75);
  }
  to {
    transform: scale(1);
  }
}

.bottle {
  position: absolute;
  transform: translateY(340px);
  z-index: -1;
  animation: fullBottle 1s linear 1.3s forwards;
}

@keyframes fullBottle {
  0% {
    transform: translateY(340px);
  }
  100% {
    transform: translateY(420px);
  }
}

.heading {
  position: absolute;
  text-align: center;
  opacity: 0;
  transform: translateY(135px);
  color: #13395b;
  word-spacing: 3px;
  animation: headingAnim 1.5s linear 1.3s forwards;
}
.sub-heading {
    position: absolute;
    text-align: center;
    opacity: 0;
    transform: translateY(280px);
    color: black;
    word-spacing: 3px;
    animation: subHeadingAnim 1.5s linear 1.3s forwards;
  }

  .kbutton{
    position: absolute;
    text-align: center;
    align-items: center;
    transform: translateY(430px);
    z-index: -2;
    background-color: #E9C46B;
    height: 30px;
    width: 120px;
    font-size: 13px;
    padding: 2px 3px;
    border-radius: 6px;

    animation: kbuttonAnim 1s linear 0s forwards;
  }
  .prebutton{
    position: absolute;
    text-align: center;
    transform: translateY(430px);
    z-index: -2;
  }

@keyframes kbuttonAnim {
    from{
        opacity: 0;
        transform: translateY(430px);
    }
    to{
        opacity: 1;
        transform: translateY(310px);
    }
}
@keyframes headingAnim {
  from {
    opacity: 0;
    font-size: 42px;
  }
  to {
    opacity: 1;
    font-size: 52px;
  }
}

@keyframes subHeadingAnim {
    from {
      opacity: 0;
      font-size: 8px;
    }
    to {
      opacity: 1;
      font-size: 12px;
    }
  }

